@import url("https://maxcdn.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css");

* {
  -webkit-transition: background-color .3s linear, color .3s ease-in;
  -o-transition: background-color .3s linear, color .3s ease-in;
  transition: background-color .3s linear, color .3s ease-in;
}

body {
  -webkit-user-select:none;
     -moz-user-select:none;
      -ms-user-select:none;
          user-select:none;
}

.editor {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.tab-bar {
  height: 38px;
  position:fixed;
  top:0;
  right:0;
  left:250px;
  background-color: rgba(255,255,255,0.03);
}

.light .tab-bar {
  background-color: rgba(0,0,0,0.03);
}

.activity-bar {
  width: 50px;
  position:fixed;
  top:0;
  bottom:24px;
  left:0;
  background-color: rgba(255,255,255,0.01);
}

.light .activity-bar {
  background-color: rgba(0,0,0,0.6);
}

.side-bar {
  width: 200px;
  position:fixed;
  top:0;
  bottom:24px;
  left:50px;
  background-color: rgba(255,255,255,0.05);
}

.light .side-bar {
  background-color: rgba(0,0,0,0.15);
}

.syntax {
  position:fixed;
  top:38px;
  right:0;
  bottom:24px;
  left:250px;
  overflow: auto;
}

.status-bar {
  height:24px;
  position:fixed;
  right:0;
  bottom:0;
  left:0;
  background-color: #3D8D90;
  color:white;
  font-size:0.75em;
}

.activity-bar__icon-list {
  list-style-type:none;
  margin:0.8em 0;
  padding:0;
  color:#ABAAAA;
}

.activity-bar__contrast-icon {
  color:rgba(255,255,255,0.05);
}

.activity-bar__normal-icon {
  color:rgba(255,255,255,0.2);
}

.activity-bar__light-icon {
  color:rgba(255,255,255,0.8);
}

.activity-bar__icon-list li {
  margin-left:0.7em;
  margin-bottom:1em;
  cursor:pointer;
}

.activity-bar__icon-list li i {
  font-size:2em;
}

.activity-bar__icon-list li:hover i {
  color:white;
}

.tab-bar__tabs-list {
  list-style-type:none;
  margin:0;
  padding:0;
}

.tab-bar__tabs-list li {
  float:left;
  padding:1em;
  font-size:0.8em;
  border-right:1px solid rgba(0,0,0,0.2);
  cursor:pointer;
  color:rgba(255,255,255,0.2);
}

.light .tab-bar__tabs-list li {
  border-right:1px solid rgba(0,0,0,0.08);
  color:rgba(0,0,0,0.2);
}

.tab-bar__tabs-list li:hover {
  background-color: rgba(255,255,255,0.05);
}

.tab-bar__tabs-list li i {
  padding:0 0.2em 0 0;
  color:rgba(255,255,255,0.5);
}

.light .tab-bar__tabs-list li i {
  color:rgba(0,0,0,0.5);
}

li.tab-bar__active-tab,
li.tab-bar__active-tab:hover {
  color:rgba(255,255,255,0.7);
  background-color: rgba(0,0,0,0.2);
}

.light li.tab-bar__active-tab,
.light li.tab-bar__active-tab:hover {
  color:rgba(0,0,0,0.6);
  background-color: rgba(0,0,0,0.12);
}

.status-bar__left-text {
  float:left;
  padding: 0.4em 1.2em;
}

.status-bar__right-text {
  float:right;
  padding: 0.4em 1.2em;
}

.side-bar__title {
  display:block;
  font-size:0.65em;
  text-transform:uppercase;
  padding:1.2em 2em;
  color:white;
}

.side-bar__theme-type {
  display:block;
  font-size:0.65em;
  font-weight:bold;
  text-transform:uppercase;
  padding:0.6em 2em;
  color:white;
  background-color:rgba(255,255,255,0.05);
}

.light .side-bar__theme-type {
  background-color:rgba(0,0,0,0.08);
}


.side-bar__themes-list {
  list-style-type:none;
  position:absolute;
  top:4.85em;
  left:0;
  right:0;
  bottom:0;
  margin:0;
  padding:0 0 2em 0;
  color:white;
  font-size:0.8em;
  overflow-x: auto;
}

.side-bar__themes-list li {
  padding:0.35em 1.8em;
  cursor:pointer;
}

.side-bar__themes-list li:hover {
  background-color:rgba(0,0,0,0.1);
}

.side-bar__themes-list li i {
  padding-right:0.3em;
  color:#F0624B;
}

li.side-bar__active-item,
li.side-bar__active-item:hover {
  background-color:rgba(0,0,0,0.3);
}

.syntax__line-numbers {
  position:absolute;
  left:0;
  top:0;
  bottom:0;
  width:40px;
  color:white;
  height:1300px;
  background-color:rgba(0,0,0,0.12);
}

.light .syntax__line-numbers {
  background-color:rgba(0,0,0,0.05);
}

.syntax__line-numbers ul {
  list-style-type:none;
  margin:0;
  padding:0;
  font-family: 'Fira Code', 'Hack', 'Source Code Pro', Monaco, monospace;
  font-size:12px;
  text-align:right;
  margin-top:0.6em;
}

.syntax__line-numbers li {
  margin-bottom:0;
  margin-right:0.5em;
  color:rgba(255,255,255,0.25);
}

.light .syntax__line-numbers li {
  color:rgba(0,0,0,0.25);
}

.syntax__edit-pane {
  position:absolute;
  left:0;
  top:0;
  bottom:0;
  right:0;
  padding: 0.5em 0 5em 60px;
  font-family: 'Fira Code', 'Hack', 'Source Code Pro', Monaco, monospace;
  font-size:12px;
  line-height:1.6em;

}


